export default "import { AfterMapping, Inject, MapMethod, MappingContext, ObjectMapperFactory } from '@sontx/mapstructjs';\n\nclass WelcomeService {\n  welcome(name: string) {\n    return `welcome ${name}`;\n  }\n}\n\nclass ObjectMapper {\n  @Inject(WelcomeService)\n  private welcomeService: WelcomeService;\n\n  @MapMethod({\n    properties: ['name', 'age'],\n  })\n  toDto: (source: any) => any;\n\n  @AfterMapping()\n  after(context: MappingContext) {\n    return {\n      ...context.target,\n      name: this.welcomeService.welcome(context.target.name),\n    };\n  }\n}\n\nconst objectMapper = new ObjectMapperFactory().create(ObjectMapper);\n\nconst staffEntity = {\n  name: 'son',\n  age: 20,\n  company: {\n    name: 'fsoft',\n    rank: {\n      number: 10,\n      type: 'good',\n    },\n  },\n};\n\nconst staffDto = objectMapper.toDto(staffEntity);\nconsole.log(staffDto);\n";